.deliveryList {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.deliveryList Input {
    border: none;
    background: #D9D9D97F;
    color: #000;
    text-align: start;
    padding: 20px;
    border-radius: 10px;
}

.deliveryList Input::placeholder {
    color: #000;
    opacity: 1;
}