.swap {
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.currencyPair {
    display: flex;
    flex-direction: column;
}

.swapButton {
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.aboutSwap {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.aboutSwap {
    display: flex;
    flex-direction: column;
    background: #EFF4FC;
    padding: 15px;
    border-radius: 10px;
    gap: 10px;
    font-family: Inter;
    font-size: 14px;
    font-weight: 500;
    line-height: 22px;
}

.aboutSwapItem {
    display: flex;
    justify-content: space-between;
}

.currencyPairItem {
    display: flex;
    flex-direction: column;
}

.swapInput {
    border: 1px solid #E8EFFB;
    border-radius: 10px;
    padding: 10px;
    display: flex;
}

.swapInput input {
    border: none;
    outline: none;
}

.swapInput .swapInputPlaceholder {
    font-family: Inter;
    font-size: 14px;
    font-weight: 500;
    line-height: 22px;
    color: #5A6876;
}
.swapInputButton {
    background: #F3F9FE;
    border: none;
    border-radius: 5px;
    padding: 10px 18px;
    display: flex;
    gap: 15px;

    font-family: Inter;
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    text-align: center;
}
.swapInputButton img {
    width: 24px;
    height: 24px;
}
.swapInputLeft, .swapInputRight {
    flex: 1;
    display: flex;
    text-align: center;
    vertical-align: center;
    align-items: center;
    gap: 20px;
}